.board {
  width: 280px;
  height: 280px;
  padding: 10px;
  -ms-touch-action: none;
      touch-action: none;
  border-radius: 6px;
  background: #bbada0;
}

.row {
  margin-bottom: 10px;
  height: 57.5px;
}

.row :last-child {
  margin-right: 0;
}

.cell {
  width: 57.5px;
  height: 57.5px;
  margin-right: 10px;
  float: left;
  border-radius: 3px;
  background: rgba(238, 228, 218, 0.35);
}

.card {
  position: absolute;
  top: -1px;
  left: -1px;
  height: 59.5px;
  width: 59.5px;
  border-radius: 3px;
  font-weight: bold;
  font-size: 20px;
  line-height: 59.5px;
  text-align: center;
  color: white;
  background-color: transparent;
  color: #776e65;
  -webkit-transition: all 0.1s ease;
  -o-transition: all 0.1s ease;
  transition: all 0.1s ease;
}

.card_number_2 {
  background: #eee4da;
}

